@import url('https://fonts.googleapis.com/css2?family=Jost:wght@300;400&family=Montserrat&family=Roboto+Slab&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Roboto Slab', serif;;
}

.app{
  width: 100%;
  height: 100vh;
  background-size: cover;
  background-position: center;
}

.overlay {
  width: 100%;
  height: 100vh;
}

.container{
  max-width: 800px;
  margin: auto;
  height: 100%;

  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
  padding: 1rem;
}

/* TOP */

.section{
  width: 100%;
  padding: 1rem;
  border-radius: 0.4rem;
  color: wheat;
  margin-top: 20px;
}

.section__inputs, .section__temperature{
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: rgb(0,0,0,0.7);
}

.section__inputs input{
  border: 0.8px soluid white;
  border-radius: 0.4rem;
  background-color: transparent;
  color: white;
  padding: 0.5rem;
  font-size: 20px;
  font-weight: 200;
}

.section__inputs input:focus{
  outline: none;
}

.section__inputs button{
  padding: 10px 50px;
  border: none;
  border-radius: 00.4rem;
  font-size: 20px;
  background-color: white;
}

.section__inputs button:hover{
  cursor: pointer;
  background-color: lightgray;
}

.icon{
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  
}

.icon h3{
  font-size: 15px;
  font-weight: 200;
  text-transform: capitalize;
}
.temperature h1{
  font-size: 60px;
}